<template>
  <div>
    <nav-bar/>
    <main class="read">
      <nav class="read-bar">
        <ul>
          <li v-for="(item,index) in menuList" :key="index" @click="doNotes(item,index)" :class="{'read-bar-choose':activeIndex==index}">
            {{item.tagName}}
          </li>
        </ul>
      </nav>
     
     
      <el-row class="read-content">
        <el-col :span="5">
          <div>
            <div class="read-content-div" draggable="true" @dragend="ondragend(index)" 
              @dragover="ondragover(index)" @drop="ondrop(index)" @dragstart="ondragstart(index)" 
              v-for="(item,index) in itemList" :key="index" @click="goDetail(item)">
              <div class="read-content-div-item">
                {{item.name}}
                <div class="over_text">
                  {{item.url}}
                </div>
              </div>
            </div>
          </div>
        </el-col>
        <transition name="readanim1">
         
          <el-col :span="19" style="padding:0 5px" v-if="renderHtml">
             <div style="padding:5px;border-left:1px solid #c9c9c9">
                <!-- <div >
                  正在阅读:{{notesTitle}}
                </div> -->
                <div class="upload" @click="handleClick('.md')">
                  <i class="iconfont icon-xiazai"></i>markdown
                </div>
                <div class="upload" @click="handleClick('.json')">
                  <i class="iconfont icon-xiazai"></i>json
                </div>
                <iframe name="myIframe" style="display:none"></iframe>
                <div v-html="renderHtml" class="markdown-body"></div>
             </div>
          </el-col>
        </transition>
      </el-row>
      
    </main>
  </div>
  
</template>

<script>
import NavBar from '../../components/navBar.vue'
export default {
  components: {
    NavBar
  },
  data () {
    return {
      menuList:[],
      itemList:[],
      startindex:'',
      endindex:'',
      activeIndex: 0,
      renderHtml: '',
      fileUrl:'',
      notesTitle:''
    }
  },
  methods:{
    handleClick(ext){
      window.open('http://localhost:3000/upload/?name='+this.fileUrl+ext, 'myIframe');
    },
    getPageScroll() {
      let xScroll, yScroll;
      if (self.pageYOffset) {
          yScroll = self.pageYOffset;
          xScroll = self.pageXOffset;
      } else if (document.documentElement && document.documentElement.scrollTop) {     // Explorer 6 Strict
          yScroll = document.documentElement.scrollTop;
          xScroll = document.documentElement.scrollLeft;
      } else if (document.body) {// all other Explorers
          yScroll = document.body.scrollTop;
          xScroll = document.body.scrollLeft;
      }
      return new Array(xScroll,yScroll);
    },
    ondragstart(index){
      this.startindex = index
      // ev.dataTransfer.setData("id",ev.target.id);
    },
    ondrop(index){
      this.endindex = index
    },
    ondragover(ev){
      event.preventDefault();
    },
    ondragend(ev){
      const start = this.itemList[this.startindex]
      const end = this.itemList[this.endindex]
      this.itemList.splice(this.startindex,1,end)
      this.itemList.splice(this.endindex,1,start)
    },
    doNotes(key,index){
      this.activeIndex = index
      this.queryNotesByTagId(key.tagId)
    },
    queryMenus(){
      this.$http.post('/edit/queryTags.next').then((res)=>{
        if(res){
          this.menuList = res.data.tags
          this.queryNotesByTagId(res.data.tags[0].tagId)
        }
      },(err)=>{
      })
    },
    queryNotesByTagId(tagId){
      this.renderHtml = ''
      this.$http.post('/edit/queryNotesByTagId.next',{tagId:tagId},{isLoading:false}).then((res)=>{
        if(res){
          this.itemList = res.data
          if(this.itemList.length>0){
            this.queryNotesHtml(this.itemList[0].url)
          }
        }
      },(err)=>{
      })
    },
    queryNotesHtml(url){
      url = url||'/notes/default'
      this.$http.post('/edit/queryNotes.next',{url:url},{isLoading:false}).then((res)=>{
        if(res){
          this.$nextTick(function () {
            this.renderHtml = res.data.render||'暂无内容'
            this.fileUrl = res.data.url
          })
        
        }
      },(err)=>{
      })
    },
    goDetail(item){
      this.notesTitle = item.name
      this.queryNotesHtml(item.url)
    },
    
    
  },
  mounted(){
    this.queryMenus()
    window.onscroll = ()=>{
      let nav = document.getElementsByTagName('nav')
      if(nav&&nav[0]){
        if(this.getPageScroll()[1]>60){
          nav[0].style.transform = "translate3d(0,-61px,0)"
        }else{
          nav[0].style.transform = "translateZ(0)"
        }
      }
    }
  }
}
</script>


<style scoped lang="scss">
.read{
  width: 80%;
  height: 100%;
  margin: 0 auto;
  background-color: #ffffff;
  &-bar{
    height: 50px;
    background-color: #ffffff;
    position: fixed;
    width: 100%;
    left:0;
    top:61px;
    z-index: 9;
    transition: all .2s;
    &-choose{
      font-weight: bold;
    }
    ul{
      height: 100%;
      margin: auto;
      display: flex;
      align-items: center;
      max-width: 960px;
    }
    li{
      list-style: none;
      float: left;
      margin-left: 20px;
      color: #71777c;
      cursor: pointer;
    }
    li:hover{
      font-weight: bold;
    }
  }
  &-content{
    .upload{
      cursor: pointer;
      border: 1px solid #e0e0e0;
      width: max-content;
      padding: 5px 10px;
      font-size: 14px;
      margin-bottom: 10px;
      display: inline-block;
      border-radius: 3px;
      i{
        margin-right:5px;
      }
    }
    margin-top: 60px;
    position: relative;
    z-index: 8;
    &-left{
      background: url('../../../static/th.jpeg') no-repeat;
      background-size: cover;
      width: 200px;
      height: 300px;
    }
    &-div{
      &-item{
        font-size: 12px;
        color: #000;
        font-weight: bold;
        padding: 5px;
        background-color: rgba(241,241,241,0.3);
        border-bottom: 1px solid rgba(241,241,241,0.8);
        cursor: pointer;
        div{
          font-weight: bold;
          font-size: 12px;
          margin-top: 5px;
          color: #b2bac2;
          cursor: text;
        }
      }
      &-item:hover{
        background-color: rgba(241,241,241,0.7);
      }
    }
  }
  .readanim1-enter-active{
    transition: all 1s;
    opacity: 1;
  }
  .readanim1-leave-active {
    transition: all 1s;
    opacity: 0;
  }
  .readanim2-enter-active{
    transition: all 1s;
    opacity: 1;
  }
  .readanim2-leave-active {
    transition: all 1s;
    opacity: 0;
  }
}
</style>


