<template>
  <div class="edit">
    <!-- <nav-bar/> -->
    <edit-nav-bar @show-tool="showTool"/>
    <save-panel :show="showPanel" @on-click-hide="hidePanel"/>
    <el-row style="height:90%;overflow: scroll;">
      <div class="edit-left" :class="{'edit-left-active':showMenu}">
        <div style="margin-top:40px">
          <div class="edit-left-list" :class="{'edit-left-choose':index==activeIndex}" v-for="(item,index) in notesList" @click="queryNotes(item.url,index)" :key="index">
            <i class="iconfont icon-color" :class="getClass(item.tagid)"></i>{{item.name}}
            <span>{{format(item.updatetime||item.createtime)}}</span>
          </div>
        </div>
      </div>
      <el-col :span="24" class="edit-right" :class="{'edit-right-active':showMenu}">
        <mavon-editor :value="markdownValue" @save="onSave" style="height:100%"/>
      </el-col>
      <div class="edit-chart" :class="{'edit-chart-active':showChart}" @click="showChart=!showChart">
          <div id="myChart"></div>
          <div id="myChart2"></div>
          <div id="myChart3"></div>
      </div>
    </el-row>
  </div>
</template>

<script>
import NavBar from '../../components/navBar.vue'
import SavePanel from './SavePanel.vue'
import EditNavBar from './EditNavBar.vue'
import Chart from './chart.js'
export default {
  components: {
    NavBar,
    EditNavBar,
    SavePanel
  },
  data () {
    return {
      markdownValue:'',
      activeIndex: 0,
      chooseUrl: '',
      showPanel: false,
      showChart: false,
      showMenu: false,
      notesList:[]
    }
  },
  methods:{
    getClass(tagid){
      switch (tagid){
        case "001":
          return "icon-vuejs"
        case "002":
          return "icon-nodejs"
        case "003":
          return "icon-logo-javascript"
        case "004":
          return "icon-angular"
        default:
          return "icon-qita"
      }
    },
    format(time){
      return this.$util._formatDate(time)
    },
    onSave(value,render){
      if(this.chooseUrl){
        if(value||this.markdownValue){
          this.$http.post('/edit/saveNotes.next',{markdownValue:value||this.markdownValue,render:render,url:this.chooseUrl}).then((res)=>{
            this.$message({
              message: 'save success',
              type: 'success'
            });
          },(err)=>{
            alert(err)
          })
        }
      }
    },
    queryNotes(url,index){
      if(url){
        this.activeIndex = index
        this.chooseUrl = url
        this.$http.post('/edit/queryNotes.next',{url:url}).then((res)=>{
          if(res){
            this.markdownValue = res.data.markdownValue
          }
        },(err)=>{
        })
      }
    },
    queryNotesAll(){
      this.$http.post('/edit/queryNotesAll.next').then(res=>{
        this.notesList = res.data
        this.queryNotes(res.data[0].url,0)
      })
    },
    queryChartData(){
      this.$http.post('/chart/queryChart.next',{type:'bar'}).then(res=>{
        if(res.success){
          Chart.drawBar(document.getElementById('myChart'),{
            title:'笔记数量统计',
            xAxis:res.data.xAxis,
            data: res.data.data
          })
          Chart.drawBar(document.getElementById('myChart2'),{
            title:'笔记数量统计',
            xAxis:res.data.xAxis,
            data: res.data.data
          })
          Chart.drawBar(document.getElementById('myChart3'),{
            title:'笔记数量统计',
            xAxis:res.data.xAxis,
            data: res.data.data
          })
        }
      })
    },
    hidePanel(){
      this.queryNotesAll()
      this.showPanel = false
    },
    showTool(menu){
      switch (menu.key){
        case 'left-menu':
          this.showMenu = !this.showMenu
          break;
        case 'panel':
          this.showPanel = !this.showPanel
          break;
        case 'chart':
          this.showChart = !this.showChart
          break;
      }
    }
  },
  mounted(){
    this.queryNotesAll()
  },
  watch:{
    showChart(newVal,oldVal){
      if(newVal){
        this.queryChartData()
      }
    }
  }
}
</script>


<style scoped lang="scss">
.edit{
  height: 100%;
  width: 100%;
  &-left{
    height: 100%;
    border-right:1px solid #e0e0e0;
    overflow: scroll;
    position: absolute;
    z-index: 1998;
    width: 200px;
    background-color: #f1f1f1;
    // background: url('../../../static/th.jpeg') no-repeat;
    background-position-x: 50%;
    transition: all .2s;
    -webkit-transition: all .2s;
    transform: translateX(-200px);
    &-active{
      transform: translateX(0px);
    }
    &-bar{
      height: 40px;
      position:absolute;
      width: inherit;
      background-color: #fff;
      color:rgba(64, 158, 255,.9);
      display: flex;
      align-items: center;
      justify-content: center;
      border-bottom: 1px solid rgba(64, 158, 255,.9);
      border-right: 1px solid rgba(64, 158, 255,.9);
      cursor: pointer;
      i{
        font-size: 25px;
      }
    }
    &-list{
      padding: 5px;
      cursor: pointer;
      border-bottom: 1px solid #f1f1f1;
      span{
        display: block;
        font-size: 12px;
        margin: 5px 0;
        color: #000;
      }
    }
    &-choose,&-list:hover{
      color: #fff;
      .icon-color{
        color: #fff;
      }
      span{
        color: #fff;
      }
    }
  }
  &-right{
    transition: all .2s;
    transform: translateX(0px);
    height:100%;
    overflow: scroll;
    &-active{
      transform: translateX(200px);
      width: 85%;
    }
  }
  &-chart{
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.4);
    transition: all .5s ease-in-out;
    transform: translateY(1000px);
    position: fixed;
    top: 0;
    left: 0;
    &-active{
      transform: translateY(60px);
    }
    div{
      display: inline-block;
      width: 33%;
    }
  }
  #myChart,#myChart2,#myChart3{
    height: 50%;
    box-sizing: border-box;
    margin: 0;
    background-color: rgba(255,255,255,1);
  }
  .edit-left-col{
    height: 100%;
  }
}
</style>


