<template>
  <el-row>
    <div class="nav-bar">
        <el-col :span="16">
          <ul>
            <li style="margin-top:-5px;" @click="goHome">
              Easy Note
              <div style="font-size:12px">easy to write notes</div>
            </li>
          </ul>
        </el-col>
        <el-col :span="8">
          <ul class="theme_panel">
            <router-link to="/read2">
              <li><i class="iconfont icon-bangzhuzhongxin"></i>笔记中心</li>
            </router-link>
            <router-link to="/edit">
              <li><i class="iconfont icon-console"></i>写文章</li>
            </router-link>
             <!-- <router-link to="/tree">
              <li><i class="iconfont icon-liucheng"></i>笔记树</li>
            </router-link> -->
            <li @click="showTheme=!showTheme">
              <i class="iconfont icon-qiehuan"></i>
            </li>
            <div class="theme_panel_div" v-show="showTheme">
              <div v-for="(item,index) in themeColor" @click="switchTheme(item)" :key="index" :style="{'background-color':item.color}">
              </div>
            </div>
          </ul>
        </el-col>
    </div>
  </el-row>
</template>
<script>
  export default {
    data() {
      return {
        activeIndex: '1',
        showTheme: false,
        themeColor:[{
          theme:'theme_blue',
          color:'rgba(64, 158, 255, .9)'
        },{
          theme:'theme_green',
          color:'#00d1b2'
        },{
          theme:'theme_pink',
          color:'#f39894'
        },{
          theme:'theme_orange',
          color:'#f4b976'
        },{
          theme:'theme_cyan',
          color:'#26b6be'
        }]
      };
    },
    methods:{
      goHome(){
        this.$router.push({name: 'Home'})
      },
      switchTheme(theme){
        this.showTheme = false
        this.$store.dispatch("switchTheme",theme.theme)
      }
    }
  }
</script>
<style scoped lang="scss">
.nav-bar{
  height: 60px;
  i{
    font-size: 20px;
    top: 3px;
    position: relative;
    margin-right: 3px;
  }
  li{
    list-style: none;
    float: left;
    margin-left: 20px;
    color: #fff;
    cursor: pointer;
  }
  li:hover{
    animation:sucaijiayuan 1s .1s ease both;
    -webkit-animation:sucaijiayuan 1s .1s ease both;
	  -moz-animation:sucaijiayuan 1s .1s ease both;
  }
  @-webkit-keyframes sucaijiayuan {
    0% {
      -webkit-transform:scale(1)
    }
    10%,20% {
      -webkit-transform:scale(0.8) rotate(-2deg)
    }
    30%,50%,70%,90% {
      -webkit-transform:scale(1.1) rotate(2deg)
    }
    40%,60%,80% {
      -webkit-transform:scale(1.1) rotate(-2deg)
    }
    100% {
      -webkit-transform:scale(1) rotate(0)
    }
  }
  .theme_panel{
    position: relative;
    &_div{
      position: absolute;
      width: 150px;
      top: 40px;
      right: 75px;
      transition: all .2s;
      border:1px solid #e0e0e0;
      background-color: rgba(0,0,0,.1);
      z-index: 99;
      div{
        display: inline-block;
        width: 33.33%;
        height: 33px;
        cursor: pointer;
        transition: all .2s;
      }
      div:hover{
        transform: scale(1.1);
      }
    }
  }
}
</style>


