<template>
  <el-row>
    <div class="nav-bar">
        <el-col :span="10" style="height:100%">
          <ul>
            <li style="margin-top:-5px;" @click="goHome">
              Easy Note
              <div style="font-size:12px">easy to write notes</div>
            </li>
          </ul>
        </el-col>
        <el-col :span="4" class="nav-bar-btn">
          <ul class="menu-center">
          </ul>
        </el-col>
        <el-col :span="10" class="nav-bar-menu">
          <ul class="menu-right">
            <li @click="$emit('show-tool',{key:'panel'})"><i class="iconfont icon-xinjian"></i>写一篇</li>
            <li @click="$emit('show-tool',{key:'left-menu'})"><i class="iconfont icon-caidan"></i>笔记</li>
            <li @click="$emit('show-tool',{key:'chart'})"><i class="iconfont icon-tongji"></i>统计</li>
            <li @click="$emit('show-tool',{key:'tool'})"><i class="iconfont icon-tongji"></i>生成图片</li>
          </ul>
        </el-col>
    </div>
  </el-row>
</template>
<script>
  export default {
    data() {
      return {
      };
    },
    methods:{
      goHome(){
        this.$router.push({path: 'read2'})
      }
    },
    mounted(){
    }
  }
</script>
<style scoped lang="scss">
.nav-bar{
  height: 60px;
  &-btn{
    height:100%;
    display: flex;
    align-items: center;
  }
  &-menu{
    display: flex;
    height:100%;
    align-items: center;
  }
  i{
    font-size: 20px;
    top: 3px;
    position: relative;
    margin-right: 3px;
  }
  li{
    list-style: none;
    float: left;
    margin-left: 20px;
    color: #fff;
    cursor: pointer;
  }
  .menu-left{
    margin-left: 30px;
    color: #fff;
    font-size: 20px;
    height: 100%;
    i{
      top: 0;
      cursor: pointer;
    }
  }
  .menu-center{
    li{
      // background-color: rgb(64, 158, 255);
      padding: 5px 25px;
      color: #fff;
      font-size:16px;
      cursor: pointer;
    }
    li:hover{
      animation:sucaijiayuan 1s .1s ease both;
      -webkit-animation:sucaijiayuan 1s .1s ease both;
	    -moz-animation:sucaijiayuan 1s .1s ease both;
    }
  }
  .menu-right li{
    cursor: pointer;
  }
  .menu-right li:hover{
    animation:sucaijiayuan 1s .1s ease both;
    -webkit-animation:sucaijiayuan 1s .1s ease both;
	  -moz-animation:sucaijiayuan 1s .1s ease both;
  }
  @-webkit-keyframes sucaijiayuan {
    0% {
      -webkit-transform:scale(1)
    }
    10%,20% {
      -webkit-transform:scale(0.8) rotate(-2deg)
    }
    30%,50%,70%,90% {
      -webkit-transform:scale(1.1) rotate(2deg)
    }
    40%,60%,80% {
      -webkit-transform:scale(1.1) rotate(-2deg)
    }
    100% {
      -webkit-transform:scale(1) rotate(0)
    }
  }
}
</style>


